"use client";

import { useEffect, useState } from "react";
import api from "../../../lib/axios";
import { useParams, useRouter } from "next/navigation";

export default function PostDetailPage() {
  const [post, setPost] = useState(null);
  const params = useParams();
  const router = useRouter();

  useEffect(() => {
    const fetchPost = async () => {
      try {
        const token = localStorage.getItem("token");
        const res = await fetch("/api/proxy/posts/" + params.id, {
          method: "GET",
          headers: {
            "Authorization": `Bearer ${token}`,
          },
        });

        const data = await res.json();
        setPost(data);
      } catch (err) {
        console.error(err);
        alert("Failed to fetch post");
        router.push("/posts");
      }
    };

    if (params.id) fetchPost();
  }, [params.id, router]);

  console.log(post);

  if (!post) return <div className="text-center mt-20">Loading...</div>;

  return (
    <div className="p-6">
      <div className="card bg-base-100 shadow-xl p-6">
        <h1 className="text-3xl font-bold mb-4">{post.title}</h1>
        <p className="mb-4 whitespace-pre-line">{post.isi}</p>

        <div className="text-sm text-gray-500 mb-6">
          <span>By: {post.user?.name || "Unknown"}</span>
          <span className="ml-4">Created at: {new Date(post.created_at).toLocaleString()}</span>
        </div>

        <div className="card-actions">
          <button
            className="btn btn-error"
            onClick={async () => {
              if (confirm("Delete this post?")) {
                try {
                  const token = localStorage.getItem("token");
                  await fetch(`/api/proxy/posts/${params.id}`, {
                    method: "DELETE",
                    headers: {
                      Authorization: `Bearer ${token}`,
                    },
                  });
                  router.push("/posts");
                } catch (err) {
                  alert("Failed to delete post");
                }
              }
            }}
          >
            Delete
          </button>
          <button
            className="btn btn-secondary"
            onClick={() => router.push(`/posts/${params.id}/edit`)}
          >
            Edit
          </button>
        </div>
      </div>
    </div>
  );
}
